/*-----------------------------------------------
|   Shape Curve
-----------------------------------------------*/

.custom-shape-divider-top-1606559984 {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  line-height: 0;
}

.custom-shape-divider-top-1606559984 svg {
  position: relative;
  display: block;
  width: calc(100% + 1.3px);
  height: 24px;
}

.custom-shape-divider-top-1606559984 .shape-fill {
  fill: #FFFFFF;
}